

import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import FastClick from 'fastclick'

import '@/util/rem'

import '@/assets/style/reset.css'
import Loading from '@/plugins/loading'
import Toast from '@/plugins/toast'

import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'



import GlobalConfig from '@/config/GlobalConfig'

Vue.use(GlobalConfig)

Vue.use(Loading)
Vue.use(Toast)

Vue.use(AlertPlugin)
Vue.use(ToastPlugin, {position: 'bottom'})
Vue.use(ConfirmPlugin)

if ('addEventListener' in document) {
document.addEventListener('DOMContentLoaded', function () {
FastClick.attach(document.body)
}, false)
}

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



