export default {
  name: 'toast',
  props: {
    message: {
      type: String,
      default: ''
    },
    className: {
      type: String,
      default: ''
    },
    position: {
      type: String,
      default: 'middle'
    },
    iconClass: {
      type: String,
      default: ''
    }
  },

  data () {
    return {
      visible: false
    }
  },

  computed: {
    customClass () {
      let classes = []
      switch (this.position) {
        case 'top':
          classes.push('is-top')
          break
        case 'middle':
          classes.push('is-middle')
          break
        case 'bottom':
          classes.push('is-bottom')
          break
        default:
          classes.push('is-middle')
      }
      classes.push(this.className)

      return classes.join(' ')
    }
  }
}



